import styled from "styled-components";



export const StyledGroupFeed =styled.section`
width: 100%;
margin-top: 10px;


.bgcPhotoContainer{
    width: 100%;
    height: 200px;
    overflow:hidden;
    border-radius:10px;

}
.bgcPhotoContainer>img{
    height:auto;
    width: 100%;
}
`